import React from "react";
import Entry from "./Entry";
import emojipedia from "../emojipedia";

function emojiTool(Tools) {
  return (
    <Entry
      key={Tools.id}
      emoji={Tools.emoji}
      name={Tools.name}
      description={Tools.meaning}
    />
  );
}
function App() {
  return (
    <div>
      <h1>
        <span>emojipedia</span>
      </h1>
      <dl className="dictionary">{emojipedia.map(emojiTool)}</dl>
    </div>
  );
}

export default App;
